import * as React from "react";
import { useState, useEffect, useRef } from "react";
import type { UnitKey, UnitSpec } from "@game/shared";
import type { UnitProductionItem } from "../../services/unitsService";

interface ShipProductionProgressProps {
  /** Current active ship production queue */
  productionQueue: UnitProductionItem[];
  /** Units catalog for displaying unit names and details */
  unitsCatalog: UnitSpec[];
  /** Production capacity (credits per hour) for time calculations */
  productionPerHour?: number;
  /** Refresh callback */
  onRefresh?: () => void;
  /** Cancel production callback */
  onCancel?: (itemId: string) => Promise<void>;
}

/**
 * ShipProductionProgress - Shows active ship production similar to structure construction
 * 
 * Features:
 * - Progress bars with real-time updates
 * - ETA display with countdown
 * - Quantity tracking (e.g., "Building Corvette 2/5")
 * - Cancel functionality
 */
const ShipProductionProgress: React.FC<ShipProductionProgressProps> = ({
  productionQueue,
  unitsCatalog,
  productionPerHour,
  onRefresh,
  onCancel,
}) => {
  // Local tick for progress bar updates without network calls
  const [, setProgressTick] = useState(0);
  const progressTimerRef = useRef<number | null>(null);

  // Start timer when there are active productions
  useEffect(() => {
    if (productionQueue.length === 0) {
      if (progressTimerRef.current) {
        window.clearInterval(progressTimerRef.current);
        progressTimerRef.current = null;
      }
      return;
    }

    // Update every second
    progressTimerRef.current = window.setInterval(() => {
      setProgressTick((x) => x + 1);
    }, 1000);

    return () => {
      if (progressTimerRef.current) {
        window.clearInterval(progressTimerRef.current);
        progressTimerRef.current = null;
      }
    };
  }, [productionQueue.length]);

  const formatEta = (completesAt: string): string => {
    const now = Date.now();
    const end = new Date(completesAt).getTime();
    const diff = Math.max(0, end - now);
    
    if (diff < 1000) return "Completed";
    
    const hours = Math.floor(diff / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((diff % (1000 * 60)) / 1000);
    
    if (hours > 0) return `${hours}h ${minutes}m ${seconds}s`;
    if (minutes > 0) return `${minutes}m ${seconds}s`;
    return `${seconds}s`;
  };

  const calculateProgress = (item: UnitProductionItem): number => {
    const now = Date.now();
    const start = new Date(item.startedAt).getTime();
    const end = new Date(item.completesAt).getTime();
    
    if (!Number.isFinite(start) || !Number.isFinite(end) || start >= end) {
      return now >= end ? 100 : 0;
    }
    
    const total = end - start;
    const elapsed = Math.max(0, Math.min(total, now - start));
    const pct = (elapsed / total) * 100;
    
    return Math.max(0, Math.min(100, pct));
  };

  const getUnitDisplayName = (unitKey: UnitKey): string => {
    const unit = unitsCatalog.find(u => u.key === unitKey);
    return unit?.name || unitKey;
  };

  const handleCancel = async (itemId: string) => {
    if (!onCancel) return;
    
    try {
      await onCancel(itemId);
      // Refresh will be handled by parent component
    } catch (error) {
      console.error('Failed to cancel production:', error);
    }
  };

  if (productionQueue.length === 0) {
    return null;
  }

  return (
    <div className="space-y-3">
      <div className="flex items-center justify-between">
        <h4 className="text-sm font-semibold text-empire-gold">Ship Production</h4>
        <div className="text-xs text-gray-400">
          {productionQueue.length} item{productionQueue.length !== 1 ? 's' : ''} in queue
        </div>
      </div>

      {productionQueue.map((item) => {
        const progress = calculateProgress(item);
        const eta = formatEta(item.completesAt);
        const unitName = getUnitDisplayName(item.unitKey);
        const isCompleted = progress >= 100;
        
        return (
          <div
            key={item.id}
            className="p-3 bg-gray-800/60 border border-gray-600 rounded-md"
          >
            <div className="flex items-center justify-between mb-2">
              <div className="text-sm text-gray-200">
                <span className="font-medium">{unitName}</span>
                {item.totalQuantity > 1 && (
                  <span className="ml-2 text-gray-400">
                    ({item.quantity}/{item.totalQuantity})
                  </span>
                )}
              </div>
              <div className="flex items-center gap-2">
                <div className={`text-sm ${isCompleted ? 'text-green-400' : 'text-yellow-300'}`}>
                  {isCompleted ? 'Completed' : `ETA: ${eta}`}
                </div>
                {onCancel && !isCompleted && (
                  <button
                    onClick={() => handleCancel(item.id)}
                    className="text-xs px-2 py-1 bg-red-600 hover:bg-red-700 text-white rounded transition-colors"
                    title="Cancel production"
                  >
                    Cancel
                  </button>
                )}
              </div>
            </div>

            {/* Progress bar */}
            <div className="w-full bg-gray-600 rounded-full h-2 mb-2">
              <div
                className={`h-2 rounded-full transition-all duration-300 ${
                  isCompleted ? 'bg-green-600' : 'bg-blue-600'
                }`}
                style={{ width: `${progress}%` }}
              />
            </div>

            {/* Additional info */}
            <div className="flex justify-between text-xs text-gray-400">
              <span>Cost: {item.creditsCost.toLocaleString()} credits</span>
              {productionPerHour && (
                <span>
                  Production: {productionPerHour.toLocaleString()} credits/hour
                </span>
              )}
            </div>
          </div>
        );
      })}

      {onRefresh && (
        <div className="text-center">
          <button
            onClick={onRefresh}
            className="text-xs px-3 py-1 bg-gray-600 hover:bg-gray-700 text-white rounded transition-colors"
          >
            Refresh Status
          </button>
        </div>
      )}
    </div>
  );
};

export default ShipProductionProgress;