import React, { useEffect, useRef } from 'react';
import { CoordinateComponents } from '@game/shared';

interface FleetPosition {
  fleetId: string;
  fleetName: string;
  coordinate: CoordinateComponents;
  isMoving: boolean;
  movement?: {
    origin: CoordinateComponents;
    destination: CoordinateComponents;
    progress: number; // 0-1
    status: string;
    eta: string;
  };
}

interface FleetMapOverlayProps {
  fleets: any[];
  canvas: HTMLCanvasElement;
  canvasSize: { width: number; height: number };
  zoomLevel: 'universe' | 'galaxy' | 'region' | 'system';
  selectedCoordinate: CoordinateComponents | null;
  onFleetClick?: (fleetId: string) => void;
}

const FleetMapOverlay: React.FC<FleetMapOverlayProps> = ({
  fleets,
  canvasSize,
  zoomLevel,
  selectedCoordinate,
  onFleetClick
}) => {
  const overlayCanvasRef = useRef<HTMLCanvasElement>(null);

  // Convert coordinate to screen position based on zoom level
  const coordinateToScreenPos = (coord: CoordinateComponents): { x: number; y: number } | null => {
    if (!selectedCoordinate) return null;

    switch (zoomLevel) {
      case 'universe':
        // Show all fleets as galaxy-level dots
        return {
          x: (coord.galaxy / 40) * canvasSize.width,
          y: (coord.galaxy / 40) * canvasSize.height
        };
        
      case 'galaxy':
        // Show fleets in the current galaxy as region-level dots
        if (selectedCoordinate?.galaxy === undefined || coord.galaxy !== selectedCoordinate.galaxy) return null;
        return {
          x: ((coord.region % 10) / 10) * canvasSize.width,
          y: (Math.floor(coord.region / 10) / 10) * canvasSize.height
        };
        
      case 'region':
        // Show fleets in the current region as system-level dots
        if (selectedCoordinate?.galaxy === undefined || selectedCoordinate?.region === undefined ||
            coord.galaxy !== selectedCoordinate.galaxy || 
            coord.region !== selectedCoordinate.region) return null;
        return {
          x: ((coord.system % 10) / 10) * canvasSize.width,
          y: (Math.floor(coord.system / 10) / 10) * canvasSize.height
        };
        
      case 'system':
        // Show fleets in the current system as body-level dots
        if (selectedCoordinate?.galaxy === undefined || selectedCoordinate?.region === undefined || selectedCoordinate?.system === undefined ||
            coord.galaxy !== selectedCoordinate.galaxy || 
            coord.region !== selectedCoordinate.region ||
            coord.system !== selectedCoordinate.system) return null;
        
        // Arrange bodies in orbital pattern
        const angle = (coord.body / 20) * 2 * Math.PI;
        const radius = 50 + (coord.body * 15); // Orbital distance
        const centerX = canvasSize.width / 2;
        const centerY = canvasSize.height / 2;
        
        return {
          x: centerX + Math.cos(angle) * radius,
          y: centerY + Math.sin(angle) * radius
        };
        
      default:
        return null;
    }
  };

  // Draw fleet overlays
  const drawFleetOverlays = () => {
    if (!overlayCanvasRef.current) return;

    const canvas = overlayCanvasRef.current;
    const ctx = canvas.getContext('2d');
    if (!ctx) return;

    // Clear canvas
    ctx.clearRect(0, 0, canvasSize.width, canvasSize.height);
    
    // Set canvas size
    canvas.width = canvasSize.width;
    canvas.height = canvasSize.height;

    fleets.forEach((fleet) => {
      // Draw stationed fleet
      const pos = coordinateToScreenPos(fleet.coordinate);
      if (pos) {
        drawFleetIcon(ctx, pos.x, pos.y, fleet, false);
      }

      // Draw movement path and moving fleet if applicable
      if (fleet.isMoving && fleet.movement) {
        const originPos = coordinateToScreenPos(fleet.movement.origin);
        const destPos = coordinateToScreenPos(fleet.movement.destination);
        
        if (originPos && destPos) {
          // Draw movement path
          drawMovementPath(ctx, originPos, destPos, fleet.movement.progress);
          
          // Draw fleet at current position along path
          const currentPos = {
            x: originPos.x + (destPos.x - originPos.x) * fleet.movement.progress,
            y: originPos.y + (destPos.y - originPos.y) * fleet.movement.progress
          };
          
          drawFleetIcon(ctx, currentPos.x, currentPos.y, fleet, true);
          
          // Draw destination marker
          drawDestinationMarker(ctx, destPos.x, destPos.y);
        }
      }
    });
  };

  // Draw individual fleet icon
  const drawFleetIcon = (
    ctx: CanvasRenderingContext2D, 
    x: number, 
    y: number, 
    fleet: FleetPosition, 
    isMoving: boolean
  ) => {
    const size = zoomLevel === 'system' ? 12 : zoomLevel === 'region' ? 8 : 6;
    
    // Fleet background circle
    ctx.beginPath();
    ctx.arc(x, y, size, 0, 2 * Math.PI);
    ctx.fillStyle = isMoving ? '#60A5FA' : '#10B981'; // Blue if moving, green if stationed
    ctx.fill();
    
    // Fleet border
    ctx.beginPath();
    ctx.arc(x, y, size, 0, 2 * Math.PI);
    ctx.strokeStyle = isMoving ? '#3B82F6' : '#059669';
    ctx.lineWidth = 2;
    ctx.stroke();
    
    // Fleet icon (small ship)
    ctx.fillStyle = '#FFFFFF';
    ctx.font = `${Math.max(8, size - 2)}px Arial`;
    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';
    ctx.fillText('', x, y);
    
    // Fleet name label (only at higher zoom levels)
    if (zoomLevel === 'system' || zoomLevel === 'region') {
      ctx.fillStyle = '#E5E7EB';
      ctx.font = '10px Arial';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'top';
      ctx.fillText(fleet.fleetName, x, y + size + 2);
    }
    
    // Movement status indicator
    if (isMoving && fleet.movement) {
      ctx.fillStyle = '#FCD34D';
      ctx.font = '8px Arial';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'bottom';
      ctx.fillText(`${Math.round(fleet.movement.progress * 100)}%`, x, y - size - 2);
    }
  };

  // Draw movement path
  const drawMovementPath = (
    ctx: CanvasRenderingContext2D,
    origin: { x: number; y: number },
    destination: { x: number; y: number },
    progress: number
  ) => {
    // Path line
    ctx.beginPath();
    ctx.moveTo(origin.x, origin.y);
    ctx.lineTo(destination.x, destination.y);
    ctx.strokeStyle = '#6366F1';
    ctx.lineWidth = 2;
    ctx.setLineDash([5, 5]);
    ctx.stroke();
    ctx.setLineDash([]);
    
    // Progress line
    const currentX = origin.x + (destination.x - origin.x) * progress;
    const currentY = origin.y + (destination.y - origin.y) * progress;
    
    ctx.beginPath();
    ctx.moveTo(origin.x, origin.y);
    ctx.lineTo(currentX, currentY);
    ctx.strokeStyle = '#10B981';
    ctx.lineWidth = 3;
    ctx.stroke();
    
    // Origin marker
    ctx.beginPath();
    ctx.arc(origin.x, origin.y, 4, 0, 2 * Math.PI);
    ctx.fillStyle = '#EF4444';
    ctx.fill();
    
    // Direction arrow
    const angle = Math.atan2(destination.y - origin.y, destination.x - origin.x);
    const arrowSize = 8;
    
    ctx.save();
    ctx.translate(currentX, currentY);
    ctx.rotate(angle);
    
    ctx.beginPath();
    ctx.moveTo(arrowSize, 0);
    ctx.lineTo(-arrowSize / 2, -arrowSize / 2);
    ctx.lineTo(-arrowSize / 2, arrowSize / 2);
    ctx.closePath();
    ctx.fillStyle = '#10B981';
    ctx.fill();
    
    ctx.restore();
  };

  // Draw destination marker
  const drawDestinationMarker = (
    ctx: CanvasRenderingContext2D,
    x: number,
    y: number
  ) => {
    const size = 8;
    
    // Pulsing effect
    const pulseSize = size + Math.sin(Date.now() / 500) * 2;
    
    ctx.beginPath();
    ctx.arc(x, y, pulseSize, 0, 2 * Math.PI);
    ctx.fillStyle = 'rgba(248, 113, 113, 0.3)';
    ctx.fill();
    
    ctx.beginPath();
    ctx.arc(x, y, size, 0, 2 * Math.PI);
    ctx.fillStyle = '#F87171';
    ctx.fill();
    
    ctx.beginPath();
    ctx.arc(x, y, size, 0, 2 * Math.PI);
    ctx.strokeStyle = '#EF4444';
    ctx.lineWidth = 2;
    ctx.stroke();
    
    // Target symbol
    ctx.fillStyle = '#FFFFFF';
    ctx.font = `${size}px Arial`;
    ctx.textAlign = 'center';
    ctx.textBaseline = 'middle';
    ctx.fillText('', x, y);
  };

  // Handle canvas clicks to detect fleet clicks
  const handleCanvasClick = (event: React.MouseEvent) => {
    if (!onFleetClick || !overlayCanvasRef.current) return;
    
    const rect = overlayCanvasRef.current.getBoundingClientRect();
    const clickX = event.clientX - rect.left;
    const clickY = event.clientY - rect.top;
    
    // Check if click is near any fleet
    for (const fleet of fleets) {
      const pos = coordinateToScreenPos(fleet.coordinate);
      if (!pos) continue;
      
      const distance = Math.sqrt(
        Math.pow(clickX - pos.x, 2) + Math.pow(clickY - pos.y, 2)
      );
      
      const clickRadius = zoomLevel === 'system' ? 15 : 10;
      if (distance <= clickRadius) {
        onFleetClick(fleet.fleetId);
        break;
      }
      
      // Also check moving fleet position
      if (fleet.isMoving && fleet.movement) {
        const originPos = coordinateToScreenPos(fleet.movement.origin);
        const destPos = coordinateToScreenPos(fleet.movement.destination);
        
        if (originPos && destPos) {
          const currentPos = {
            x: originPos.x + (destPos.x - originPos.x) * fleet.movement.progress,
            y: originPos.y + (destPos.y - originPos.y) * fleet.movement.progress
          };
          
          const moveDistance = Math.sqrt(
            Math.pow(clickX - currentPos.x, 2) + Math.pow(clickY - currentPos.y, 2)
          );
          
          if (moveDistance <= clickRadius) {
            onFleetClick(fleet.fleetId);
            break;
          }
        }
      }
    }
  };

  // Redraw when dependencies change
  useEffect(() => {
    drawFleetOverlays();
  }, [fleets, canvasSize, zoomLevel, selectedCoordinate]);

  // Animation loop for moving fleets
  useEffect(() => {
    const hasMovingFleets = fleets.some(f => f.isMoving);
    if (!hasMovingFleets) return;

    const animationInterval = setInterval(() => {
      drawFleetOverlays();
    }, 1000); // Update every second

    return () => clearInterval(animationInterval);
  }, [fleets]);

  return (
    <canvas
      ref={overlayCanvasRef}
      className="absolute inset-0 pointer-events-none"
      style={{
        width: canvasSize.width,
        height: canvasSize.height,
        zIndex: 10,
        pointerEvents: onFleetClick ? 'auto' : 'none'
      }}
      onClick={handleCanvasClick}
    />
  );
};

export default FleetMapOverlay;
